// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import i18n from './i18n/i18n';
import App from './App';
import router from './router';
import axios from 'axios';
import * as types from './store/types';
import store from './store/store';
import { SERVER_API_URL } from '@/conf/constvar';

Vue.use(ElementUI);

import '@/assets/css/index.styl';
Vue.config.productionTip = false;

// 
router.beforeEach((to, from, next)=>{
  store.state.contentLoading=false;
  if(store.state.pswdreminder)store.state.pswdreminder.close();//
  
  if(to.meta.requireAuth){
    if(window.sessionStorage.getItem("mytoken")){
      next();
    }else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  }else{
    next();
  }
});

//
showConfirm(); //
export function showConfirm(){
  const activeTime = sessionStorage.getItem('expires_in')*1000;//token
  if(!activeTime){ 
    return false;
  }
  store.state.LoginResTime=setTimeout(()=>{
    store.state.loginResShow=true;
  },activeTime)
}

//axios.defaults.headers.common['Authorization'] = "AUTH_TOKEN";
// http request 
axios.interceptors.request.use(
  config => {
    const mytoken = sessionStorage.getItem('mytoken');
    if(mytoken) {  // tokenhttp headertoken
      config.headers.Authorization = `Bearer ${mytoken}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
});

// http response 
var isRefreshingFlag = true;//tokenisRefreshingFlag//https://blog.csdn.net/w13707470416/article/details/85222337
axios.interceptors.response.use(
  response => {//
    return response
  },
  err => {
    if(err.response) { //tokentokentokenloginRestrictiontokenloginRestriction
      switch (err.response.status) {
        case 401://token401(err.config)tokentokentoken
        let config = err.config;
        if(isRefreshingFlag){
          refreshTokenRequst();
        }
        isRefreshingFlag = false;
        const retryOriginalRequest = new Promise((resolve) => {
          addSubscriber(()=> {
            resolve(axios(config))
          })
        });
        return retryOriginalRequest;
        break;
        default:
        apps.$message.error('');
      }
    }else{
      apps.$message.error('');
    }
    return Promise.reject(err)// return
  }
);

//token 
function refreshTokenRequst(){
  const oldRefreshToken = sessionStorage.getItem('refresh_token');
  var url=SERVER_API_URL+"uaa/oauth/token?refresh_token="+oldRefreshToken+"&grant_type=refresh_token";
  const instance = axios.create();//
  instance.post(url,{},{
    headers: {
      'Authorization': 'Basic VUk6V0VScU0zWnVORFIxZEhWQw==',
    }
  }).then((response)=>{
    isRefreshingFlag = true;
    store.commit(types.LOGIN,response.data);
    onAccessTokenFetched();
  }).catch(err=>{
    isRefreshingFlag = true;
    alert(",");
    store.commit(types.LOGOUT);
  })
}

//token
let subscribers = [];
function onAccessTokenFetched() {
  subscribers.forEach((callback)=>{
    callback();
  })
  subscribers = [];
}

//token
function addSubscriber(callback) {
  subscribers.push(callback)
}

var apps = new Vue({
  el: '#app',
  router,
  store,
  i18n,
  template: '<App/>',
  components: { App }
});






