import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'
import axios from 'axios'
import {SERVER_API_URL} from '@/conf/constvar';
import client from "@/components/es/connection";
import toISO8601 from '@/assets/js/iso8601';
import { Loading } from 'element-ui';
import {handleConfigure} from '@/assets/js/tool';
import { Message } from 'element-ui';
import router from '@/router';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    productName:'',
    user: {
      name:''
    },
    useTime:'',//tokentokentoken
    refresh_token: '',
    token: "",

    pswdreminder:null,//
    showEditUsersPswdDialog:false,// 
    userInfo:{},

    contentLoading:false,//loading

    myVersion:'',//
    myNotification:false,//
    title: '',
    isMenuShow:false,//echars resize
    // isShowTimerate:false,//
    // mytimeInterval:300000,//300000
    // mapMagterval:60000,//60000
    
    alreadyhasdomian:false,//   
    autoconfigport:'',// 
    autoconfiginput:'',///ip 
    autoconfigsnodeid:'',

    licenseTimer:null,
    licenceinfo:{},
    
    nowTimeVal:[new Date(new Date().getTime()-24*60*60*1000), new Date()],
    
    mydatedata:{
      starttime:'',
      endtime:'',
      interval:'5m',//5m(MYDATEPICKER)
      flowInterval:'600s',//10s
      timestampnow:''
    },
    timerateOptions: [{//
      value: '5m',
      label: '5'
    }, {
      value: '1h',
      label: ''
    }, {
      value: '1d',
      label: ''
    }
    ],
    timerateValue: '',
    dateTimeLong:24*60*60*1000,
   
    configServerUrl:'',//url

    allRulesetData:[],

    //
    fieldYOptions:[],
    
    //
    loginResShow:false,//
    LoginResTime:null,//tokenloginRestriction
    restrictFlag:false,//
    
    // 
    refreshTimeout:null,//
    refreshAble:false,//    
    refreshFalg:false,//loadingloading

    //es
    esDomainArr:[],//
    getEsDomainArrLoading:false,

    esUrlArr:[],//url
    getEsUrlArrLoading:false,

  },
  mutations:{
    [types.LOGIN]:(state,data)=>{//
      // console.log(data)
      state.token=data.access_token;
      state.useTime=data.expires_in;
      state.refresh_token=data.refresh_token;
      sessionStorage.setItem('mytoken',data.access_token);
      sessionStorage.setItem('refresh_token',data.refresh_token);
      sessionStorage.setItem('expires_in',data.expires_in);
      state.isMenuShow=false;
    },
    [types.LOGOUT]:(state)=>{//
      // sessionStorage.removeItem('mytoken');
      // window.localStorage.clear();
      window.sessionStorage.clear();
      clearTimeout(state.LoginResTime);
      clearTimeout(state.refreshTimeout);
      clearTimeout(state.licenseTimer);
      state.user.name='';
      state.token='';
      state.contentLoading=false;
      if(state.restrictFlag){//
        setTimeout(() => {
          alert('');
        },0);
        state.restrictFlag = false;
      }
      router.replace({
        path: 'login',
        query: { redirect: router.currentRoute.fullPath }//
      });
    },
    [types.USERNAME]:(state,data)=>{//
      sessionStorage.setItem('myname',data);
      state.user.name=data;
    },
    [types.GETLICENCEINFO]:(state,data)=>{//Token license

      // var url=SERVER_API_URL+"license/status";
      // axios.get(url).then((res)=>{
      //   if(res.data.responseCode==0){
      //     // console.log(res)
      //     state.licenceinfo=res.data.data;
      //   }
      // }); 

      var url=SERVER_API_URL+"license/status";
      state.licenseTimer=null;
      function _timeFn(){
        let nowTime=new Date().getTime();
        axios.get(url).then((res)=>{
          if(res.data.responseCode==0){
             let resData=res.data.data;
             state.licenceinfo=resData;
             if(resData.expireDate-nowTime<7*24*60*60*1000)
             {
              state.licenceinfo.istobeexpired=true;
             }else{
              state.licenceinfo.istobeexpired=false;
             }
            
            state.licenseTimer=setTimeout(()=>{
              _timeFn()
            },60000)
          }
        }); 
      }
      _timeFn();
    },
    [types.GETVERSION]:(state,data)=>{//
      var url=SERVER_API_URL+"pe/version";
      axios.get(url).then((res)=>{
        if(res.data.responseCode==0){
          state.myVersion=res.data.data;
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    // [types.GETNOTIFICATION]:(state,data)=>{// notification
    //   var url=SERVER_API_URL+"monitoring/quota/param";
    //   axios.get(url).then((res)=>{
    //     if(res.data.responseCode==0){
    //       state.myNotification=res.data.data.notification;
    //     }
    //   }).catch((err)=>{
    //     console.log(err);
    //   })
    // },

    // [types.LICENCEINFO]:(state,data)=>{w
    //   state.licenceinfo=data;
    // },
    [types.MYDATEPICKER]:(state)=>{// //
      state.mydatedata.starttime=state.nowTimeVal[0];
      state.mydatedata.endtime=state.nowTimeVal[1]; 
      //
      var sTime = state.nowTimeVal[0].getTime();
      var eTIme = state.nowTimeVal[1].getTime();
      var differenceValue = eTIme-sTime;
      if(differenceValue>5*365*24*60*60*1000){//>5 ~365
        state.mydatedata.interval="5d";
      }else if(differenceValue<=5*365*24*60*60*1000 && differenceValue>2*365*24*60*60*1000){//<= && > 1825~730
        state.mydatedata.interval="1d";
      }else if(differenceValue<=2*365*24*60*60*1000 && differenceValue>365*24*60*60*1000){//<= && > 3504~1752
        state.mydatedata.interval="5h";
      }else if(differenceValue<=365*24*60*60*1000 && differenceValue>180*24*60*60*1000){//<= && > 8760~4380
        state.mydatedata.interval="1h";
      }else if(differenceValue<=180*24*60*60*1000 && differenceValue>90*24*60*60*1000){//<= && > 8640~4320
        state.mydatedata.interval="30m";
      }else if(differenceValue<=90*24*60*60*1000 && differenceValue>30*24*60*60*1000){//<= && > 8640~2880
        state.mydatedata.interval="15m";
      }else if(differenceValue<=30*24*60*60*1000 && differenceValue>1*60*60*1000){//<= && > 8640~30
        state.mydatedata.interval="5m";
      }else if(differenceValue<=1*60*60*1000 && differenceValue>10*60*1000){//<=1 && >10 360~60
        state.mydatedata.interval="10s";
      }else if(differenceValue<=10*60*1000 && differenceValue>=0){//<=10 >=0 600~0
        state.mydatedata.interval="1s";
      }
      // console.log(state.mydatedata.interval);
     
      // state.mydatedata.flowInterval=state.mydatedata.flowInterval;
      state.mydatedata.timestampnow=new Date().getTime();
    },
    // [types.CONTENTICONLOADING]:(state,flag)=>{
    //   state.CONTENTICONLOADING=flag;
    // },
    [types.MAKEPDF]:(state,data)=>{// pdf
      const blob = new Blob([data.pdfdata],{type: "application/pdf"});
      if (window.navigator.msSaveOrOpenBlob) {
        navigator.msSaveBlob(blob, data.pdfname);
      } else {
        var link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        link.download = data.pdfname;
        //
        document.body.appendChild(link);
        var evt = document.createEvent("MouseEvents");
        evt.initEvent("click", false, false);
        link.dispatchEvent(evt);
        document.body.removeChild(link);
      }
    },

    //WAF
    [types.GETALLRULESET]:(state,type)=>{
      state.allRulesetData=[];
      const url=SERVER_API_URL+"waf/ruleset";
      axios.get(url).then((res)=>{
        if(res.data.responseCode==0){
          state.allRulesetData=res.data.data;
        }else{
          // this.$message.error(': '+ res.data.data.message);
          Message({
            message:':'+ res.data.data.message,
            type:'error'
          })
        }
      }).catch((err)=>{
        console.log(err)
      });
    },
    //
    [types.GETFIELDOPTIONS]:(state,type)=>{//fieldYOptions
      // console.log(type);
      state.fieldYOptions=[]//
      if(type=='date_histogram'){
        state.fieldYOptions=[{
          value:'Data',
          disabled: true
        },{
          value:'@timestamp'
        }]
      }else{
        const url =SERVER_API_URL+"dp/es/fieldcaps";
        axios.get(url).then(res => {
          // console.log(res);
          if(res.data.responseCode==0){
            const numberArray = res.data.data.Number;
            const stringArray = res.data.data.String;

            if(type=="terms" || type=="max" || type=="min" || type=='cardinality'){
              state.fieldYOptions.push({
                value:'Data',
                disabled: true
              },{
                value:'@timestamp'
              })
            }
           
            if(numberArray.length!=0){
              state.fieldYOptions.push({
                value:'Number',
                disabled: true
              });
              numberArray.forEach(element => {
                state.fieldYOptions.push({
                  value:element
                })
              });
            };

            if(type=='terms' || type=='cardinality'){
              if(stringArray.length!=0){
                state.fieldYOptions.push({
                  value:'String',
                  disabled: true
                });
                stringArray.forEach(element => {
                  state.fieldYOptions.push({
                    value:element
                  })
                });
              }
            }
          }
        }).catch(err=>{
          console.log(err);
        })
      }
    },

    //es
    [types.GETESDOMAINS]:(state,flag)=>{//
      state.esDomainArr=[];
      state.getEsDomainArrLoading=true;
      const sttime=state.mydatedata.starttime,
            edtime=state.mydatedata.endtime;
      const fromTime2=toISO8601(sttime),
            endTime2=toISO8601(edtime);
      const fromTime=fromTime2 || toISO8601(new Date(Date.now()-state.dateTimeLong)),
            endTime=endTime2 || toISO8601(new Date());

      let decryptedObj={
            // "query_string": {
            //   "query": "shadow.access.decrypted:1"
            // }
          };
      let mustObj=[];
      if(flag){//-decrypted:1 or api_indicator:1 
        mustObj=[
          {
            "query_string": {
              "query": "shadow.access.error_code:0"
            }
          },
          {
            "query_string": {
              "query": "shadow.access.response_code:200"
            }
          },
          {
            "bool": {
              "should": [
                {
                  "query_string": {
                    "query": "shadow.access.decrypted:1"
                  }
                },
                {
                  "query_string": {
                    "query": "shadow.access.api_indicator:1"
                  }
                }
              ]
            }
          },
          {
            "range": {
              "@timestamp": {
                "gte":fromTime,
                "lte":endTime,
              }
            }
          }
        ]
      }else{
        mustObj=[
          {
            "query_string": {
              "query": "shadow.access.error_code:0"
            }
          },
          {
            "query_string": {
              "query": "shadow.access.response_code:200"
            }
          },
          {
            "range": {
              "@timestamp": {
                "gte":fromTime,
                "lte":endTime,
              }
            }
          }
        ]
      };
      
      client.search({
        index:"shadow-filebeat*",
        body:{
          "size": 0,
          "query": {
            "bool": {
              "must": mustObj,
              "must_not": [
                {
                  "match": {
                    "shadow.access.token": "00000000000000000000000000000000"
                  }
                },
                {
                  "match": {
                    "shadow.access.token":"aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa"
                  }
                }
              ]
            }
          },
          "aggs": {
            "GroupByDomain": {
              "terms": {
                "field": "shadow.access.http_host",
                "size": 100
              }
            }
          }
        }
      }).then((res)=>{
        // console.log(res)
        state.getEsDomainArrLoading=false;
        // const resData=[{
        //   "key": "mall.geely.com",
        //   "doc_count": 326
        // }, {
        //   "key": "www.sherry.com",
        //   "doc_count": 68
        // }, {
        //   "key": "www.lensy.com",
        //   "doc_count": 2
        // }]
        const resData=res.aggregations?res.aggregations.GroupByDomain.buckets:[];
        state.esDomainArr=resData;
      });

    },
    [types.GETESURLS]:(state,data)=>{//url
      // console.log(data);
      state.esUrlArr=[];
      state.getEsUrlArrLoading=true;
      const sttime=state.mydatedata.starttime,
            edtime=state.mydatedata.endtime;
      const fromTime2=toISO8601(sttime),
            endTime2=toISO8601(edtime);
      const fromTime=fromTime2 || toISO8601(new Date(Date.now()-state.dateTimeLong)),
            endTime=endTime2 || toISO8601(new Date());
      const domainVal = data.domain;

      let decryptedObj={
        "query_string": {
          "query": "shadow.access.decrypted:1"
        }
      };
      if(data.flag){//-decrypted:1api_indicator:1
        decryptedObj={
          "bool": {
            "should": [
              {
                "query_string": {
                  "query": "shadow.access.decrypted:1"
                }
              },
              {
                "query_string": {
                  "query": "shadow.access.api_indicator:1"
                }
              }
            ]
          }
        }
      };

      client.search({
        index:"shadow-filebeat*",
        body:{
          "size": 0,
          "query": {
            "bool": {
              "must": [
                {
                  "query_string": {
                    "query": "shadow.access.error_code:0"
                  }
                },
                {
                  "query_string": {
                    "query": "shadow.access.response_code:200"
                  }
                },
                decryptedObj,
                {
                  "term": {
                    "shadow.access.http_host": domainVal
                  }
                },
                {
                  "range": {
                    "@timestamp": {
                      "gte":fromTime,
                      "lte": endTime,
                    }
                  }
                }
              ],
              "must_not": [
                {
                  "match": {
                    "shadow.access.token": "00000000000000000000000000000000"
                  }
                },
                {
                  "match": {
                    "shadow.token.token":"aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa"
                  }
                },
                {
                  "query_string": {
                    "query": "shadow.access.org_url:\\/40*\\.html"
                  }
                }
              ]
            }
          },
          "aggs": {
            "GroupByUrl": {
              "terms": {
                "field": "shadow.access.org_url",
                "size": 100
              }
            }
          }
        }
      }).then((res)=>{
        // console.log(res)
        state.getEsUrlArrLoading=false;
        const resData=res.aggregations.GroupByUrl.buckets;
        // const resData=[
        //   {
        //     "key": "/getapi",
        //     "doc_count": 37
        //   },
        //   {
        //     "key": "/pe/shadow/snode",
        //     "doc_count": 13
        //   },
        // ]
        state.esUrlArr=resData;
      });
    },
  }
});


